.marker-icon {
  cursor: pointer;
  fill: #ffa500;
  -webkit-transition: 0.3s all;
  -o-transition: 0.3s all;
  transition: 0.3s all;
}

.marker-icon:hover {
  -webkit-transform: scale(1.3);
      -ms-transform: scale(1.3);
          transform: scale(1.3);
}

.MyMarker {

  -webkit-transition: 1s all;

  -o-transition: 1s all;

  transition: 1s all;
}

.MyMarker .active {
  -webkit-transform: scale(1.2);
      -ms-transform: scale(1.2);
          transform: scale(1.2);
  fill: #ffff00;
}


